// animation
$transition: 0.2s ease-in-out;

// break points
$small: 576px;
$medium: 768px;
$large: 992px;
$x-large: 1200px;

// positions - flex / grid
$justify-positions: (
  'justify-center': center,
  'justify-between': space-between,
  'justify-around': space-around,
);

$items-positions: (
  'items-center': center,
  'items-start': flex-start,
  'items-end': flex-end,
);

$sizes: (
  '1': 0.5rem,
  '2': 1rem,
  '3': 2rem,
  '4': 4rem,
  '5': 5rem,
);

$app-bg: linear-gradient(
  135deg,
  hsla(212, 36%, 79%, 1) 0%,
  hsla(202, 27%, 45%, 1) 100%
);
